<script>
import LayoutWebMobile from '@layouts/LayoutWebMobile'
import GxWizard from '@components/_CoreTaxes/Wizard/GxWizard'
import GxLedgerAccounts from '@components/_CoreTaxes/LedgerAccounts/GxLedgerAccounts.vue'
import GxLoader from '@components/_Core/Utils/GxLoader'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
// import { CONFIG_LOAD_CHART_OF_ACCOUNTS_FROM_TEMPLATE } from '@src/apollo/mutations'
import {
  GET_ACCOUNTING_FIRMS,
  GET_LEDGER_ACCOUNTS_BEGINNING_BALANCE,
} from '@src/apollo/queries'

import {
  CONFIG_BEGINNING_BALANCE_DELETE_ALL,
  CONFIG_SET_BEGINNING_BALANCE_FRON_FILE,
} from '@src/apollo/mutations'

export default {
  name: 'BeginningBalance',
  components: {
    LayoutWebMobile,
    GxWizard,
    GxLedgerAccounts,
    GxLoader,
    GxCardModal,
  },
  data() {
    return {
      accountingFirm: null,
      accountingFirmNumber: null,
      clientNumber: null,
      loading: false,
      error: null,
      steps: [
        'Elige tu catálogo',
        'Personaliza tu catálogo',
        'Periodo inicial',
        'Saldos iniciales',
        '¡Listo!',
      ],
      ledgerAccounts: null,
      levels: null,
      skipLedgerAccounts: true,
      errorDescription: '',
    }
  },
  apollo: {
    myAccountingFirms: {
      query: GET_ACCOUNTING_FIRMS,
    },
    getLedgerAccounts: {
      query: GET_LEDGER_ACCOUNTS_BEGINNING_BALANCE,
      variables() {
        return {
          clientNumber: [this.clientNumber],
        }
      },
      update(data) {
        this.accountingFirmNumber =
          data.myAccountingFirms[0].accountingFirmNumber
        this.ledgerAccounts =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.ledgerAccounts
        this.levels =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.levels
        if (this.levels === 0 && this.processId === null) {
          this.$router.push({
            name: 'templates',
          })
        }
      },
      skip() {
        return this.skipLedgerAccounts
      },
      fetchPolicy: 'no-cache',
    },
  },
  props: {
    file64: {
      type: String,
      default: null,
    },
  },
  created() {
    this.clientNumber = localStorage.getItem('companyNumber')
    return this.skipLedgerAccounts
  },
  mounted() {
    this.typeTemplate = localStorage.getItem('typeTemplate')
  },
  watch: {
    myAccountingFirms: {
      handler(newVal) {
        if (newVal) {
          this.accountingFirm = newVal[0]
          if (this.file64) this.sendFileBeginningBalance()
          else this.skipLedgerAccounts = false
        }
      },
    },
  },
  methods: {
    async sendFileBeginningBalance() {
      try {
        const response = await this.$apollo.mutate({
          mutation: CONFIG_SET_BEGINNING_BALANCE_FRON_FILE,
          variables: {
            accountingFirmNumber: this.accountingFirm.accountingFirmNumber,
            clientNumber: this.clientNumber,
            file: this.file64,
          },
        })
        if (
          response.data.configSetBeginningBalanceFromFile.error !== null &&
          response.data.configSetBeginningBalanceFromFile.ledgerAccounts ===
            null
        ) {
          this.errorDescription =
            response.data.configSetBeginningBalanceFromFile.error.message
          if (
            response.data.configSetBeginningBalanceFromFile.error.details !==
            null
          )
            if (
              response.data.configSetBeginningBalanceFromFile.error.details
                .length > 0
            )
              this.errorDescription +=
                ', ' +
                response.data.configSetBeginningBalanceFromFile.error.details.join(
                  ', '
                )
          this.$root.$emit('setTemporaryFile64', '')
          this.skipLedgerAccounts = false
          return
        } else {
          this.$root.$emit('setTemporaryFile64', '')
          this.skipLedgerAccounts = false
        }
      } catch (error) {
        this.errorDescription = this.$utils.parseServerError(error)
        return null
      }
    },
    goNext() {
      this.$router.push({
        name: 'finishConfiguration',
      })
    },
    goToPreviewTemplate(type) {
      this.$router.push({
        name: 'previewTemplate',
        params: { typeTemplateProps: type },
      })
    },
    closeModel() {
      this.errorDescription = ''
    },
    async beginningBalanceDeleteAll() {
      this.loading = true

      try {
        const response = await this.$apollo.mutate({
          mutation: CONFIG_BEGINNING_BALANCE_DELETE_ALL,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
          },
        })

        if (
          response.data.configBeginningBalanceDeleteAll.successful &&
          response.data.configBeginningBalanceDeleteAll.error === null
        ) {
          localStorage.setItem('backBeginningBalance', true)
          this.$router.push({
            name: 'initialPeriod',
          })
        } else {
          this.errorDescription =
            response.data.configBeginningBalanceDeleteAll.error.message +
            ' - ' +
            response.data.configBeginningBalanceDeleteAll.error.details.join('')
        }
        this.loading = false

        console.log(response)
      } catch (error) {
        console.log(error)
        return null
      }
    },
  },
}
</script>
<template>
  <LayoutWebMobile
    icon="keyboard_backspace"
    :icon-click-handler="beginningBalanceDeleteAll"
  >
    <div class="flex flex-col w-full items-center">
      <div class="flex flex-col text-xl mt-3"
        ><span class="font-black">Configuración Inicial</span></div
      >
      <div class="flex flex-col mt-10">
        <GxWizard :stepsProp="steps" :stepActiveProp="4" />
      </div>
      <div class="flex flex-col text-base mt-12"
        ><span class="font-black">Carga tus saldos iniciales</span></div
      >
      <div v-if="this.levels === null" class="flex flex-col w-full">
        <GxLoader class="mt-20 mb-5" />
      </div>
      <div class="flex flex-col w-full items-start px-8">
        <div class="flex flex-col items-stretch w-full mt-5">
          <GxLedgerAccounts
            v-if="ledgerAccounts"
            class="mt-5 mb-15 w-full"
            :accountingFirmNumberProp="accountingFirmNumber"
            :clientNumberProp="clientNumber"
            :ledgerAccountsProp="ledgerAccounts"
            :editCatalog="false"
            :showCollapsed="true"
            :showArrowCollapse="false"
            :editBeginningBalances="true"
            :isInitial="true"
          />
        </div>
      </div>
    </div>

    <div
      class="flex flex-col fixed w-full bottom-0 items-start border-t h-19 shadow-8 bg-white"
    >
      <div class="flex fixed w-50 inline">
        <GxButton
          :variant="!loading"
          :ghost="!loading"
          class="ml-10 mt-4 rounded-md gx-button"
          :loading="loading"
          @click="beginningBalanceDeleteAll"
        >
          REGRESAR
        </GxButton>
      </div>
      <div class="flex fixed w-50 inline right-0">
        <GxButton :variant="true" class="mr-10 mt-4 gx-button" @click="goNext">
          SIGUIENTE
        </GxButton>
      </div>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error al cargar los saldos iniciales'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </LayoutWebMobile>
</template>
